.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;

  &--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock {
  position: fixed;

  overflow-y: auto;
}

.btn-reset {
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
}

.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  max-width: 386px;
  width: 100%;
  height: 60px;

  font-size: 14px;
  line-height: 18px;
  border-radius: 10px;
}

.btn-rich-blue {
  color: $color-default-white;
  background-color: $color-rich-blue;
  transition: background-color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-blue-gem;
  }
}

.btn-white {
  color: $color-blackcurrant;
  background-color: $color-default-white;
  border: 1px solid $color-selago;
  transition: background-color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-selago;
  }
}

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}
