  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
