        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    padding: 40px;
    background-color: #ffffff; }
    @media (max-width: 767px) {
      .modal__content {
        width: 100%;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 15px;
    right: 15px; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    display: none; }
  .modal--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

