.login {
  position: relative;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  padding-top: 55px;
  padding-bottom: 30px;
  background-color: $color-whisper;

  @include viewport--md {
    padding-top: 40px;
    background-color: $color-default-white;
  }

  .login__info {
    position: absolute;
    bottom: 30px;
    width: 100%;
    display: flex;

    @include viewport--md {
      bottom: 35px;
    }
  }

  a {
    margin: 0 auto;
    padding: 0;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    transition: color $default-transition-settings;

    &:hover,
    &:focus,
    &:active {
      color: $color-blackcurrant;
    }
  }
}

.login__content {
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 27%;
  min-width: 335px;
  min-height: 670px;
  margin: 0 auto;

  @include viewport--md {
    min-height: 580px;
  }

  @include viewport--tn {
    min-width: 300px;
  }

  img {
    width: 82.71px;
    height: 68px;
    margin-bottom: 58px;
  }

  h1 {
    margin: 0;
    margin-bottom: 40px;
    padding: 0;

    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: $color-blackcurrant;
    text-align: center;
  }

  p {
    max-width: 319px;
    margin: 0 auto;
    margin-bottom: 42px;
    padding: 0;
    text-align: center;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
  }

  &--forgot-password {
    img {
      margin-bottom: 88px;
    }

    h1 {
      margin-bottom: 20px;
    }
  }

  &--success {
    img {
      margin-bottom: 182px;
    }

    h1 {
      @include viewport--md {
        margin-bottom: 20px;
      }
    }
  }

  &--signup,
  &--req {
    @include viewport--md {
      min-height: 700px;
    }

    img {
      margin-bottom: 33px;
    }

    h1 {
      margin-bottom: 20px;
    }

    p {
      max-width: 333px;
      margin-bottom: 38px;
    }
  }

  &--req-success {
    img {
      margin-bottom: 153px;
    }

    h1 {
      max-width: 248px;
      margin-bottom: 19px;
    }
  }
}

.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  input {
    width: 100%;
    height: 60px;
    margin-bottom: 20px;
    padding: 19.2px 21px 2px 22px;

    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    border: 1px solid $color-selago;
    border-radius: 10px;
    transition: border $default-transition-settings;

    &:hover,
    &:focus,
    &:active {
      border: 1px solid $color-rich-blue;
    }

    &:disabled {
      background-color: $color-ghost-white;
      pointer-events: none;
    }
  }

  a {
    margin-bottom: 40px;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    transition: color $default-transition-settings;

    &:hover,
    &:focus,
    &:active {
      color: $color-blackcurrant;
    }
  }

  button {
    margin-bottom: 10px;
  }

  &--signup {
    button {
      margin-top: 20px;
    }
  }
}

.login__inst {
  margin-bottom: 15%;

  svg {
    margin-right: 9px;
  }
}

.login__form-item {
  position: relative;
  width: 100%;
  height: 60px;
  margin-bottom: 20px;

  &--forgot-password {
    margin-bottom: 42px;
  }

  .login__label {
    position: absolute;
    left: 21px;
    top: 27px;
    display: flex;
    transform: translate(0, -50%);
    transition: top $default-transition-settings;
    pointer-events: none;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
  }

  p {
    margin: 0;
    padding: 0;
    transition: transform $default-transition-settings;
  }

  span {
    position: absolute;
    bottom: -16px;
    left: 21px;
    font-style: italic;
    font-size: 10px;
    line-height: 12px;
    color: $color-flamingo;

    @include viewport--tn {
      left: 10px;
      font-size: 9px;
    }
  }
}

input.on-focus + .login__label p {
  transform: translateY(-12px);
}

input.on-error {
  border: 1px solid $color-flamingo;

  &:hover,
  &:focus,
  &:active {
    border: 1px solid $color-flamingo;
  }
}

input.on-valid {
  border: 1px solid $color-selago;

  &:hover,
  &:focus,
  &:active {
    border: 1px solid $color-rich-blue;
  }
}
